	module.exports = {"data":{"postsJson":{"title":"Power, Servanthood & Persecution","questionsTitle":"OPENING THE WORD","display":"block","bookName":"Mark","ref":"6:1-7:37","html":"<p>   <strong>6</strong><u>1</u>He went out from there. He came into his own country, and his disciples followed him. <u>2</u>When the Sabbath had come, he began to teach in the synagogue, and many hearing him were astonished, saying,</p><h1>THE CROWD</h1><h4>Where did this man get these things?</h4><p>and,</p><h1>THE CROWD</h1><h4>What is the wisdom that is given to this man, that such mighty works come about by his hands? <u>3</u>Isn’t this the carpenter, the son of Mary, and brother of James, Joses, Judah, and Simon? Aren’t his sisters here with us?</h4><p>They were offended at him. <br />   <u>4</u>Jesus said to them,</p><h1>JESUS</h1><h2>A prophet is not without honor, except in his own country, and among his own relatives, and in his own house.</h2><p><u>5</u>He could do no mighty work there, except that he laid his hands on a few sick people, and healed them. <u>6</u>He marveled because of their unbelief. <br />   He went around the villages teaching. <u>7</u>He called to himself the twelve, and began to send them out two by two; and he gave them authority over the unclean spirits. <u>8</u>He commanded them that they should take nothing for their journey, except a staff only: no bread, no wallet, no money in their purse, <u>9</u>but to wear sandals, and not put on two tunics. <u>10</u>He said to them,</p><h1>JESUS</h1><h2>Wherever you enter into a house, stay there until you depart from there. <u>11</u>Whoever will not receive you nor hear you, as you depart from there, shake off the dust that is under your feet for a testimony against them. Assuredly, I tell you, it will be more tolerable for Sodom and Gomorrah in the day of judgment than for that city!</h2><p>   <u>12</u>They went out and preached that people should repent. <u>13</u>They cast out many demons, and anointed many with oil who were sick, and healed them. <u>14</u>King Herod heard this, for his name had become known, and he said,</p><h1>SOME PEOPLE</h1><h4>John the Baptizer has risen from the dead, and therefore these powers are at work in him.</h4><p><u>15</u>But others said,</p><h1>SOME PEOPLE</h1><h4>He is Elijah.</h4><p>Others said,</p><h1>SOME PEOPLE</h1><h4>He is a prophet, or like one of the prophets.</h4><p><u>16</u>But Herod, when he heard this, said,</p><h1>HEROD ANTIPAS</h1><h4>This is John, whom I beheaded. He has risen from the dead.</h4><p><u>17</u>For Herod himself had sent out and arrested John, and bound him in prison for the sake of Herodias, his brother Philip’s wife, for he had married her. <u>18</u>For John said to Herod,</p><h1>JOHN THE BAPTIST</h1><h4>It is not lawful for you to have your brother’s wife.</h4><p><u>19</u>Herodias set herself against him, and desired to kill him, but she couldn’t, <u>20</u>for Herod feared John, knowing that he was a righteous and holy man, and kept him safe. When he heard him, he did many things, and he heard him gladly. <br />   <u>21</u>Then a convenient day came, that Herod on his birthday made a supper for his nobles, the high officers, and the chief men of Galilee. <u>22</u>When the daughter of Herodias herself came in and danced, she pleased Herod and those sitting with him. The king said to the young lady,</p><h1>HEROD ANTIPAS</h1><h4>Ask me whatever you want, and I will give it to you.</h4><p><u>23</u>He swore to her,</p><h1>HEROD ANTIPAS</h1><h4>Whatever you shall ask of me, I will give you, up to half of my kingdom.</h4><p>   <u>24</u>She went out, and said to her mother,</p><h1>HERODIAS THE YOUNGER</h1><h4>What shall I ask?</h4><p>   She said,</p><h1>HERODIAS THE ELDER</h1><h4>The head of John the Baptizer.</h4><p>   <u>25</u>She came in immediately with haste to the king, and asked,</p><h1>HERODIAS THE YOUNGER</h1><h4>I want you to give me right now the head of John the Baptizer on a platter.</h4><p>   <u>26</u>The king was exceedingly sorry, but for the sake of his oaths, and of his dinner guests, he didn’t wish to refuse her. <u>27</u>Immediately the king sent out a soldier of his guard, and commanded to bring John’s head, and he went and beheaded him in the prison, <u>28</u>and brought his head on a platter, and gave it to the young lady; and the young lady gave it to her mother. <br />   <u>29</u>When his disciples heard this, they came and took up his corpse, and laid it in a tomb. <br />   <u>30</u>The apostles gathered themselves together to Jesus, and they told him all things, whatever they had done, and whatever they had taught. <u>31</u>He said to them,</p><h1>JESUS</h1><h2>You come apart into a deserted place, and rest awhile.</h2><p>For there were many coming and going, and they had no leisure so much as to eat. <u>32</u>They went away in the boat to a deserted place by themselves. <u>33</u>They* saw them going, and many recognized him and ran there on foot from all the cities. They arrived before them and came together to him. <u>34</u>Jesus came out, saw a great multitude, and he had compassion on them, because they were like sheep without a shepherd, and he began to teach them many things. <u>35</u>When it was late in the day, his disciples came to him, and said,</p><h1>THE DISCIPLES</h1><h3>This place is deserted, and it is late in the day. <u>36</u>Send them away, that they may go into the surrounding country and villages, and buy themselves bread, for they have nothing to eat.</h3><p>   <u>37</u>But he answered them,</p><h1>JESUS</h1><h2>You give them something to eat.</h2><p>   They asked him,</p><h1>THE DISCIPLES</h1><h3>Shall we go and buy two hundred denarii* worth of bread, and give them something to eat?</h3><p>   <u>38</u>He said to them,</p><h1>JESUS</h1><h2>How many loaves do you have? Go see.</h2><p>   When they knew, they said,</p><h1>THE DISCIPLES</h1><h3>Five, and two fish.</h3><p>   <u>39</u>He commanded them that everyone should sit down in groups on the green grass. <u>40</u>They sat down in ranks, by hundreds and by fifties. <u>41</u>He took the five loaves and the two fish, and looking up to heaven, he blessed and broke the loaves, and he gave to his disciples to set before them, and he divided the two fish among them all. <u>42</u>They all ate, and were filled. <u>43</u>They took up twelve baskets full of broken pieces and also of the fish. <u>44</u>Those who ate the loaves were* five thousand men. <br />   <u>45</u>Immediately he made his disciples get into the boat, and to go ahead to the other side, to Bethsaida, while he himself sent the multitude away. <u>46</u>After he had taken leave of them, he went up the mountain to pray. <br />   <u>47</u>When evening had come, the boat was in the middle of the sea, and he was alone on the land. <u>48</u>Seeing them distressed in rowing, for the wind was contrary to them, about the fourth watch of the night he came to them, walking on the sea,* and he would have passed by them, <u>49</u>but they, when they saw him walking on the sea, supposed that it was a ghost, and cried out; <u>50</u>for they all saw him, and were troubled. But he immediately spoke with them, and said to them,</p><h1>JESUS</h1><h2>Cheer up! It is I!* Don’t be afraid.</h2><p><u>51</u>He got into the boat with them; and the wind ceased, and they were very amazed among themselves, and marveled; <u>52</u>for they hadn’t understood about the loaves, but their hearts were hardened. <br />   <u>53</u>When they had crossed over, they came to land at Gennesaret, and moored to the shore. <u>54</u>When they had come out of the boat, immediately the people recognized him, <u>55</u>and ran around that whole region, and began to bring those who were sick, on their mats, to where they heard he was. <u>56</u>Wherever he entered, into villages, or into cities, or into the country, they laid the sick in the marketplaces, and begged him that they might touch just the fringe* of his garment; and as many as touched him were made well. <br />   <strong>7</strong><u>1</u>Then the Pharisees and some of the scribes gathered together to him, having come from Jerusalem. <u>2</u>Now when they saw some of his disciples eating bread with defiled, that is unwashed, hands, they found fault. <u>3</u>(For the Pharisees and all the Jews, don’t eat unless they wash their hands and forearms, holding to the tradition of the elders. <u>4</u>They don’t eat when they come from the marketplace unless they bathe themselves, and there are many other things, which they have received to hold to: washings of cups, pitchers, bronze vessels, and couches.) <u>5</u>The Pharisees and the scribes asked him,</p><h1>JEWISH LEADERS</h1><h4>Why don’t your disciples walk according to the tradition of the elders, but eat their bread with unwashed hands?</h4><p>   <u>6</u>He answered them,</p><h1>JESUS</h1><h2>Well did Isaiah prophesy of you hypocrites, as it is written, <br />   ‘This people honors me with their lips, <br />   but their heart is far from me. <br />   <u>7</u>But they worship me in vain, <br />   teaching as doctrines the commandments of men.’* <br />   <u>8</u>For you set aside the commandment of God, and hold tightly to the tradition of men—the washing of pitchers and cups, and you do many other such things.</h2><p><u>9</u>He said to them,</p><h1>JESUS</h1><h2>Full well do you reject the commandment of God, that you may keep your tradition. <u>10</u>For Moses said, ‘Honor your father and your mother;’* and, ‘He who speaks evil of father or mother, let him be put to death.’* <u>11</u>But you say, ‘If a man tells his father or his mother, “Whatever profit you might have received from me is Corban*, that is to say, given to God”;’ <u>12</u>then you no longer allow him to do anything for his father or his mother, <u>13</u>making void the word of God by your tradition, which you have handed down. You do many things like this.</h2><p>   <u>14</u>He called all the multitude to himself, and said to them,</p><h1>JESUS</h1><h2>Hear me, all of you, and understand. <u>15</u>There is nothing from outside of the man, that going into him can defile him; but the things which proceed out of the man are those that defile the man. <u>16</u>If anyone has ears to hear, let him hear!</h2><p>   <u>17</u>When he had entered into a house away from the multitude, his disciples asked him about the parable. <u>18</u>He said to them,</p><h1>JESUS</h1><h2>Are you also without understanding? Don’t you perceive that whatever goes into the man from outside can’t defile him, <u>19</u>because it doesn’t go into his heart, but into his stomach, then into the latrine, thus purifying all foods*?</h2><p><u>20</u>He said,</p><h1>JESUS</h1><h2>That which proceeds out of the man, that defiles the man. <u>21</u>For from within, out of the hearts of men, proceed evil thoughts, adulteries, sexual sins, murders, thefts, <u>22</u>covetings, wickedness, deceit, lustful desires, an evil eye, blasphemy, pride, and foolishness. <u>23</u>All these evil things come from within, and defile the man.</h2><p>   <u>24</u>From there he arose, and went away into the borders of Tyre and Sidon. He entered into a house, and didn’t want anyone to know it, but he couldn’t escape notice. <u>25</u>For a woman, whose little daughter had an unclean spirit, having heard of him, came and fell down at his feet. <u>26</u>Now the woman was a Greek, a Syrophoenician by race. She begged him that he would cast the demon out of her daughter. <u>27</u>But Jesus said to her,</p><h1>JESUS</h1><h2>Let the children be filled first, for it is not appropriate to take the children’s bread and throw it to the dogs.</h2><p>   <u>28</u>But she answered him,</p><h1>SYRO-PHOENICIAN WOMAN</h1><h4>Yes, Lord. Yet even the dogs under the table eat the children’s crumbs.</h4><p>   <u>29</u>He said to her,</p><h1>JESUS</h1><h2>For this saying, go your way. The demon has gone out of your daughter.</h2><p>   <u>30</u>She went away to her house, and found the child having been laid on the bed, with the demon gone out. <br />   <u>31</u>Again he departed from the borders of Tyre and Sidon, and came to the sea of Galilee, through the middle of the region of Decapolis. <u>32</u>They brought to him one who was deaf and had an impediment in his speech. They begged him to lay his hand on him. <u>33</u>He took him aside from the multitude, privately, and put his fingers into his ears, and he spat, and touched his tongue. <u>34</u>Looking up to heaven, he sighed, and said to him,</p><h1>JESUS</h1><h2>Ephphatha!</h2><p>that is, “Be opened!” <u>35</u>Immediately his ears were opened, and the impediment of his tongue was released, and he spoke clearly. <u>36</u>He commanded them that they should tell no one, but the more he commanded them, so much the more widely they proclaimed it. <u>37</u>They were astonished beyond measure, saying,</p><h1>THE CROWD</h1><h4>He has done all things well. He makes even the deaf hear, and the mute speak!</h4><h5>*Mark 6:33 TR reads “The multitudes” instead of “They” <br />*Mark 6:37 200 denarii was about 7 or 8 months wages for an agricultural laborer. <br />*Mark 6:44 TR adds “about” <br />*Mark 6:48 see Job 9:8 <br />*Mark 6:50 or, “I AM!” <br />*Mark 6:56 or, tassel <br />*Mark 7:7 Isaiah 29:13 <br />*Mark 7:10 Exodus 20:12; Deuteronomy 5:16 <br />*Mark 7:10 Exodus 21:17; Leviticus 20:9 <br />*Mark 7:11 Corban is a Hebrew word for an offering devoted to God. <br />*Mark 7:19 or, “making all foods clean”. NU ends Jesus' direct quote and question after “latrine”, ending the verse with “Thus he declared all foods clean. </h5>","path":"/319","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?","prevPath":"/318","nextPath":"/320"}},"pathContext":{}}

